@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
html, body, #root {
  min-height: 100vh;

}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --board-section-size: 40px;
  --board-background-color: #24135F;
  --board-background-color-wood-secondary: #261a0d;
  --board-background-url: url(https://i.dlpng.com/static/png/1927438-pitch-soccer-field-background-green-football-field-background-image-green-grass-football-in-grass-png-390_260_preview.webp);
  --board-background-url-wood: url("https://previews.123rf.com/images/prapann/prapann1606/prapann160600110/58202559-old-wood-vintage-wood-wall-texture-wood-background-old-panels.jpg");
  --board-background-color-secondary: #9A7611;

}

.BoardContainer {
  width: 1000px;
  height: 500px;
  border: 2.0vmax solid transparent;
  border-radius: 1.0vmax;
  background-color: #24135F;
  background-color: var(--board-background-color);
  box-shadow: 0 15px 10px #9A7611;
  box-shadow: 0 15px 10px var(--board-background-color-secondary);
  position: relative;
}

.Board {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  background-image: url(https://i.dlpng.com/static/png/1927438-pitch-soccer-field-background-green-football-field-background-image-green-grass-football-in-grass-png-390_260_preview.webp);
  background-image: var(--board-background-url);
}

.Board-Row1 {
  flex: 1 1;
  display: flex;
  flex-direction: row-reverse;
  /*box-shadow: 0px 0px 0px 2px white;*/
  border-top: 1px solid white;
  border-right: 1px solid white;
  border-left: 1px solid white;
  border-bottom: 1px dashed white;
}

.Board-Row2 {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  /*box-shadow: 0px 2px 0px 2px white;*/
  border-bottom: 1px solid white;
  border-right: 1px solid white;
  border-left: 1px solid white;
}

.Board-Col1, .Board-Col2 {
  flex: 1 1;
  background-image: url(https://i.dlpng.com/static/png/1927438-pitch-soccer-field-background-green-football-field-background-image-green-grass-football-in-grass-png-390_260_preview.webp);
  background-image: var(--board-background-url);
  position: relative;
  border: 20px solid transparent;
}

.Board-Col1::after, .Board-Col2::after {
  content: '';
  position: absolute;
  background-color: #24135F;
  background-color: var(--board-background-color);
  width: 90%;
  height: 90%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  box-shadow: 0px 2px 2px 2px #9A7611;
  box-shadow: 0px 2px 2px 2px var(--board-background-color-secondary);
}

.BoardSpace {
  flex: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  position: relative;
}


.BoardSpace:nth-child(2n) {
  background-color: #9A7611;
  background-color: var(--board-background-color-secondary);
}

.BoardSpace.arrow-up {
  justify-content: flex-end;
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
          clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.BoardSpace.arrow-down {
  justify-content: flex-start;
  -webkit-clip-path: polygon(100% 0%, 0% 0%, 50% 100%);
          clip-path: polygon(100% 0%, 0% 0%, 50% 100%)
}

.GameButton {
  position: absolute;
  margin: auto;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  background: transparent;
  border: 0;
}

.GameButton img {
  width: 150px;
  transition: 0.5s linear all;
}

.GameButton:focus {
  outline: #261a0d;
}

.GameButton:hover img {
  transform: scale(1.2, 1.2);
}

.Piece {
  position: relative;
  display: block;
  height: 35px;
  width: 35px;
  margin: -5px;
  border: 2px solid saddlebrown;
  background-color: antiquewhite;
  border-radius: 100px;
  overflow: visible;
}


.Piece-1 {
  border: 2px solid saddlebrown;
  background-color: antiquewhite;
}


.Piece-2 {
  border: 2px solid #24135F;
  background-color: rebeccapurple;
}


.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: rgba(0,0,0,0.1);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


#balloon-container {
  height: 100vh;
  padding: 1em;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
}

.balloon {
  height: 125px;
  width: 105px;
  border-radius: 75% 75% 70% 70%;
  position: relative;
}

.balloon:before {
  content: "";
  height: 75px;
  width: 1px;
  padding: 1px;
  background-color: #FDFD96;
  display: block;
  position: absolute;
  top: 125px;
  left: 0;
  right: 0;
  margin: auto;
}

.balloon:after {
  content: "▲";
  text-align: center;
  display: block;
  position: absolute;
  color: inherit;
  top: 120px;
  left: 0;
  right: 0;
  margin: auto;
}

@-webkit-keyframes float {
  from {transform: translateY(100vh);
    opacity: 1;}
  to {transform: translateY(-300vh);
    opacity: 0;}
}

@keyframes float {
  from {transform: translateY(100vh);
    opacity: 1;}
  to {transform: translateY(-300vh);
    opacity: 0;}
}


#balloon-container {
  position: fixed;
  z-index: 1000;
}

.birthday {
  position: absolute;
  top: 10vh;
  margin: auto;
  right: 0;
  left: 0;
  font-size: 3vh;
  font-family: Bungee;
  text-decoration: none;
  transition: all 0.5s;
}

.birthday {
  color: rebeccapurple;
  text-shadow: 5px 5px rgba(0,0,0,0.1);
}

.birthday:hover {
  color: #FF1177;
}




